
.container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  max-width: 430px;
  width: 90%;
  background: #fff;
  border-radius: 7px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.3);
   color: #525252
}

.container .form {
  padding: 2rem;
}
.form header {
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
  margin-bottom: 1.5rem;

}
.form input {
  height: 60px;
  width: 100%;
  padding: 0 15px;
  font-size: 17px;
  margin-bottom: 1.3rem;
  border: 1px solid #ddd;
  border-radius: 6px;
  outline: none;
}
.form input:focus {
      border: 1px solid #ddd;
}
.form a {
  font-size: 16px;
  color: #2691d9;
  text-decoration: none;
}
.form a:hover {
  text-decoration: underline;
}
.button {
  color: #fff;
  background: #2691d9;
  font-size: 1.2rem;
  font-weight: 500;
  letter-spacing: 1px;
  margin-top: 1.7rem;
  cursor: pointer;
  transition: 0.4s;

  height: 60px;
  width: 100%;
  padding: 0 15px;
  margin-bottom: 1.3rem;
  border: 1px solid #ddd;
  border-radius: 6px;
  outline: none;
}
.button:hover {
  background:  #2691d9;
}
.signup {
  font-size: 17px;
  text-align: center;
}
.signup label {
  color: #2691d9;
  cursor: pointer;
}
.signup label:hover {
  text-decoration: underline;
}
